html,body,main,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video,input,textarea{margin:0;padding:0;border:0;background:none;font-size:100%;font-weight:400;-webkit-font-smoothing:antialiased;-webkit-tap-highlight-color:rgba(20,23,28,0)}html{overflow-x:hidden;height:100%}*{outline:none;box-sizing:border-box;text-rendering:optimizeLegibility}*:active,*:hover,*:focus{outline:0;outline-offset:0}*::-moz-focus-inner{border:0}body{font-size:16px;height:100%}a{text-decoration:none;color:black}img{display:block;max-width:100%}label,a,input,textarea{outline:none;-webkit-tap-highlight-color:rgba(0,0,0,0);box-shadow:none}figure{margin:0}button{border:none;cursor:pointer;outline:none;background:transparent;padding:0}

.box{display:flex;flex-direction:column}.box-inp{max-width:900px;width:100%;height:52px;border:1px solid #ececec;margin-bottom:16px;display:flex;align-items:center;justify-content:space-between}.custom-checkbox{position:absolute;z-index:-1;opacity:0}.custom-checkbox+label{display:inline-flex;align-items:center;-webkit-user-select:none;-ms-user-select:none;user-select:none;font-family:"Open Sans",sans-serif;font-size:14px;line-height:19px;color:#373737;margin-left:12px}.custom-checkbox+label::before{content:"";display:inline-block;width:16px;height:16px;flex-shrink:0;flex-grow:0;border:1px solid #ececec;border-radius:4px;margin-right:0.5em;background-repeat:no-repeat;background-position:center center;background-size:50% 50%}.custom-checkbox:checked+label::before{border-color:#550dc9;background-color:#550dc9;background-image:url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e")}.custom-checkbox:not(:disabled):not(:checked)+label:hover::before{border-color:#8e4df7}.icons{display:flex;align-items:center;padding-right:12px}

